<script>

import {mapGetters, mapActions} from "vuex";

export default {
  name: "EditProfile",

  data() {
    return {
      firstname: "",
      lastname: "",
      username: "",
      email: "",
      password: "",
      verifyPassword: "",
      id: null,
      roleSelected: "",
      roles: ['user', 'manager', 'supermanager']
    };
  },

  computed: {
    ...mapGetters('user', ['userSelected', 'currentUser']),
    checkPassword () {
      return this.password === this.verifyPassword;
    },
    getUser () {
      return this.userSelected;
    },
  },

  watch: {
    getUser(newVal) {
      if (newVal && newVal.data) {
        this.username = newVal.data.username;
        this.email = newVal.data.email;
        this.id = newVal.data.id;
        this.firstname = newVal.data.firstname;
        this.lastname = newVal.data.lastname;
        this.roleSelected = newVal.data.role;
      }
    }
  },

  mounted() {
    console.log(this.getUser)
      this.username = this.getUser.data.username;
      this.email = this.getUser.data.email;
      this.id = this.getUser.data.id;
      this.firstname = this.getUser.data.firstname;
      this.lastname = this.getUser.data.lastname;
      this.roleSelected = this.getUser.data.role;
  },


  beforeUnmount() {
    this.refreshUser();
  },

  methods: {
    ...mapActions('user', ['refreshUser', 'update', 'delete']),

    async deleteUser() {
      await this.delete(this.getUser.data.id);
    },

    async updateUser() {
      if (!this.verifyPassword) {
        alert('les mots de passe ne correspondent pas')
        return;
      } else {
        const user = {
          users: {
            username: this.username,
            email: this.email,
            password: this.password,
            firstname: this.firstname,
            lastname: this.lastname,
            role: this.roleSelected
          }

        };
        await this.update({id: this.getUser.data.id, user})
      }
    }
  },
};
</script>

